.navbar{
  background-image: linear-gradient(to right, #a116ef, #390999);
  position: relative;
  padding: 5px;
  z-index: 999;
  border-radius: 0 0 30px 30px;
  box-shadow: 0 2px 5px rgba(0,0,0,0.2);
}

.navbar_logo{
   max-width: 300px;
   
  height: auto;
  line-height: 60px;
  padding: 0 20px;
  box-sizing: border-box;
  float: left;
}

nav{
 
  float: right;
  position: relative;
  width:604px;
 
  display: -webkit-flex;
 
  display: flex;
  margin: 40px;
  /* height: 50px; */
  font-size:20px;
   margin-left: auto;
}

.clearfix{
  clear: both;
}

.menu-toggle{
  display: none;
}

nav a{
  font-size: 20px;
  color: white;
  display: block;
  text-decoration: none;
  position: relative;
  z-index: 1;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin: 25px;
  font-family:'Montserrat';
}
nav a:hover{
  color:white;
}

nav .animation{
  position: absolute;
  height: 5px;
  bottom: 16px;
  z-index: 0;
  background: gold;
  border-radius: 8px;
  transition: all .7s ease 0s;
}

a:nth-child(1){
  width: 130px;
}
nav .start-home,a:nth-child(1):hover~.animation{
  width: 85px;
  left: 10px;
}
a:nth-child(2){
  width: 130px;
}
nav .start-user,a:nth-child(2):hover~.animation{
  width: 85px;
  left: 130px;
}
a:nth-child(3){
  width: 130px;
}
nav .start-hospital,a:nth-child(3):hover~.animation{
  width: 115px;
  left: 239px;
}
a:nth-child(4){
  width: 130px;
}
nav .start-aboutus,a:nth-child(4):hover~.animation{
  width: 104px;
  left: 376px;
}
a:nth-child(5){
  width: 130px;
}
nav .start-collaborate,a:nth-child(5):hover~.animation{
  width:110px;
  left: 490px;
}


@-webkit-keyframes fadeInAnimation {
  from { 
    opacity: 0;
    height: 10px;
  }
  to   { 
    opacity: 1; 
    height: 210px;
  }
}


@keyframes fadeInAnimation {
  from { 
    opacity: 0;
    height: 10px;
  }
  to   { 
    opacity: 1; 
    height: 210px;
  }
}

@-webkit-keyframes fadeOutAnimation {
  from { 
    opacity: 1; 
    height: 210px;
  }
  to   { 
    opacity: 0;
    height: 10px;
  }
}

@keyframes fadeOutAnimation {
  from { 
    opacity: 1; 
    height: 210px;
  }
  to   { 
    opacity: 0;
    height: 10px;
  }
}
@-webkit-keyframes fadeInAnimation32 {
  from { 
    opacity: 0;
    height: 0;
    top: 40%;
    width: 0;
    left: 50%;
  }
  to   { 
    opacity: 1; 
    height: 40%;
    top: 20%;
    width: 80%;
    left: 10%;
  }
}
@keyframes fadeInAnimation32 {
  from { 
    opacity: 0;
    height: 0;
    top: 40%;
    width: 0;
    left: 50%;
  }
  to   { 
    opacity: 1; 
    height: 40%;
    top: 20%;
    width: 80%;
    left: 10%;
  }
}

.animating_popup{
  position: absolute;
  left: 10%;
  top: 20%;
  width: 80%;
  height: 40%;
  -webkit-animation: fadeInAnimation32 1s;
          animation: fadeInAnimation32 1s;
}


